import React from 'react';
import { Download, Eye, FileX } from 'lucide-react';
import { createClient } from '@/lib/supabase/client';

interface ReceiptDownloadButtonProps {
  itemId: number;
  receiptUrl?: string | null;
  itemName: string;
  paymentCategory?: string | null; // ÌòÑÏû•Í≤∞Ï†ú Ïó¨Î∂Ä ÌôïÏù∏Ïö©
  onUpdate?: () => void;
}

/**
 * ÏõπÏï±Ïö© ÏòÅÏàòÏ¶ù Îã§Ïö¥Î°úÎìú Î≤ÑÌäº
 * Íµ¨Îß§ Îã¥ÎãπÏûêÍ∞Ä Î™®Î∞îÏùº Ïï±ÏóêÏÑú ÏóÖÎ°úÎìúÌïú ÏòÅÏàòÏ¶ùÏùÑ ÌôïÏù∏/Îã§Ïö¥Î°úÎìúÌï† Ïàò ÏûàÏäµÎãàÎã§
 */
export const ReceiptDownloadButton: React.FC<ReceiptDownloadButtonProps> = ({
  itemId,
  receiptUrl,
  itemName,
  paymentCategory,
  onUpdate,
}) => {
  const [isLoading, setIsLoading] = React.useState(false);

  // ÌòÑÏû•Í≤∞Ï†úÍ∞Ä ÏïÑÎãàÎ©¥ ÌëúÏãú ÏïàÌï®
  if (paymentCategory !== 'ÌòÑÏû• Í≤∞Ï†ú' && paymentCategory !== 'ÌòÑÏû•Í≤∞Ï†ú') {
    return null;
  }

  /**
   * ÏòÅÏàòÏ¶ù Îã§Ïö¥Î°úÎìú Ìï∏Îì§Îü¨
   */
  const handleDownload = async () => {
    if (!receiptUrl) return;

    setIsLoading(true);

    try {
      // URLÏóêÏÑú ÌååÏùº Í≤ΩÎ°ú Ï∂îÏ∂ú
      const url = new URL(receiptUrl);
      const pathSegments = url.pathname.split('/');
      const bucketIndex = pathSegments.indexOf('receipt-images');
      
      if (bucketIndex === -1) {
        throw new Error('ÏûòÎ™ªÎêú ÏòÅÏàòÏ¶ù URLÏûÖÎãàÎã§');
      }
      
      const filePath = pathSegments.slice(bucketIndex + 1).join('/');

      console.log('üì• ÏòÅÏàòÏ¶ù Îã§Ïö¥Î°úÎìú ÏãúÏûë:', filePath);

      // Supabase StorageÏóêÏÑú Îã§Ïö¥Î°úÎìú
      const supabase = createClient();
      const { data, error } = await supabase.storage
        .from('receipt-images')
        .download(filePath);

      if (error) throw error;

      // BlobÏùÑ Îã§Ïö¥Î°úÎìú Í∞ÄÎä•Ìïú URLÎ°ú Î≥ÄÌôò
      const blob = new Blob([data], { type: 'image/jpeg' });
      const downloadUrl = window.URL.createObjectURL(blob);

      // Îã§Ïö¥Î°úÎìú Ìä∏Î¶¨Í±∞
      const link = document.createElement('a');
      link.href = downloadUrl;
      link.download = `ÏòÅÏàòÏ¶ù_${itemName}_${itemId}_${Date.now()}.jpg`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);

      // Î©îÎ™®Î¶¨ Ìï¥Ï†ú
      window.URL.revokeObjectURL(downloadUrl);

      console.log('‚úÖ ÏòÅÏàòÏ¶ù Îã§Ïö¥Î°úÎìú ÏôÑÎ£å');
      
      // ÏÑ±Í≥µ Î©îÏãúÏßÄ (ÏÑ†ÌÉùÏ†Å)
      if (typeof window !== 'undefined' && window.alert) {
        alert('‚úÖ ÏòÅÏàòÏ¶ùÏù¥ Îã§Ïö¥Î°úÎìúÎêòÏóàÏäµÎãàÎã§.');
      }
    } catch (error) {
      console.error('‚ùå ÏòÅÏàòÏ¶ù Îã§Ïö¥Î°úÎìú Ïò§Î•ò:', error);
      alert(`‚ùå Îã§Ïö¥Î°úÎìú Ïã§Ìå®: ${error instanceof Error ? error.message : error}`);
    } finally {
      setIsLoading(false);
    }
  };

  /**
   * ÏòÅÏàòÏ¶ù ÏÉà ÌÉ≠ÏóêÏÑú Î≥¥Í∏∞
   */
  const handleViewInNewTab = () => {
    if (!receiptUrl) return;
    window.open(receiptUrl, '_blank', 'noopener,noreferrer');
  };

  /**
   * ÏòÅÏàòÏ¶ù ÎØ∏Î¶¨Î≥¥Í∏∞ Î™®Îã¨
   */
  const handlePreview = () => {
    if (!receiptUrl) return;

    // Í∞ÑÎã®Ìïú Î™®Îã¨Î°ú Ïù¥ÎØ∏ÏßÄ ÌëúÏãú
    const modal = document.createElement('div');
    modal.style.cssText = `
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.9);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 9999;
      cursor: pointer;
    `;

    const img = document.createElement('img');
    img.src = receiptUrl;
    img.style.cssText = `
      max-width: 90%;
      max-height: 90%;
      object-fit: contain;
      border-radius: 8px;
    `;

    modal.appendChild(img);
    modal.onclick = () => document.body.removeChild(modal);
    document.body.appendChild(modal);
  };

  // ÏòÅÏàòÏ¶ùÏù¥ ÏóÜÎäî Í≤ΩÏö∞
  if (!receiptUrl) {
    return (
      <div className="flex items-center gap-2 text-gray-400">
        <FileX className="w-4 h-4" />
        <span className="text-sm">ÏòÅÏàòÏ¶ù ÏóÜÏùå</span>
      </div>
    );
  }

  // ÏòÅÏàòÏ¶ùÏù¥ ÏûàÎäî Í≤ΩÏö∞
  return (
    <div className="flex items-center gap-2">
      {/* ÎØ∏Î¶¨Î≥¥Í∏∞ Î≤ÑÌäº */}
      <button
        onClick={handlePreview}
        className="inline-flex items-center gap-1.5 px-3 py-1.5 text-sm font-semibold text-white bg-blue-500 hover:bg-blue-600 rounded-md transition-colors"
        title="ÏòÅÏàòÏ¶ù ÎØ∏Î¶¨Î≥¥Í∏∞"
      >
        <Eye className="w-4 h-4" />
        <span>Î≥¥Í∏∞</span>
      </button>

      {/* Îã§Ïö¥Î°úÎìú Î≤ÑÌäº */}
      <button
        onClick={handleDownload}
        disabled={isLoading}
        className="inline-flex items-center gap-1.5 px-3 py-1.5 text-sm font-semibold text-white bg-green-500 hover:bg-green-600 disabled:bg-gray-400 disabled:cursor-not-allowed rounded-md transition-colors"
        title="ÏòÅÏàòÏ¶ù Îã§Ïö¥Î°úÎìú"
      >
        <Download className="w-4 h-4" />
        <span>{isLoading ? 'Îã§Ïö¥Î°úÎìú Ï§ë...' : 'Îã§Ïö¥Î°úÎìú'}</span>
      </button>
    </div>
  );
};

/**
 * ÏÇ¨Ïö© ÏòàÏãú (Î∞úÏ£º ÏÉÅÏÑ∏ Î™®Îã¨ - ÌòÑÏû•Í≤∞Ï†úÎßå):
 * 
 * import { ReceiptDownloadButton } from '@/components/purchase/ReceiptDownloadButton';
 * 
 * // ÌòÑÏû•Í≤∞Ï†úÏù∏ Í≤ΩÏö∞ÏóêÎßå ÌÖåÏù¥Î∏î Ïª¨Îüº Ï∂îÍ∞Ä:
 * {purchase.payment_category === 'ÌòÑÏû• Í≤∞Ï†ú' && <TableHead>ÏòÅÏàòÏ¶ù</TableHead>}
 * 
 * {purchase.payment_category === 'ÌòÑÏû• Í≤∞Ï†ú' && (
 *   <TableCell>
 *     <ReceiptDownloadButton
 *       itemId={item.id}
 *       receiptUrl={item.receipt_image_url}
 *       itemName={item.item_name}
 *       paymentCategory={purchase.payment_category}
 *       onUpdate={() => refetch()}
 *     />
 *   </TableCell>
 * )}
 */

