
import { useState } from "react";
import { X, Edit2, Save, Plus, Trash2 } from "lucide-react";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { format } from "date-fns";
import { createClient } from "@/lib/supabase/client";
import { toast } from "sonner";
import { ReceiptDownloadButton } from "./ReceiptDownloadButton";

interface PurchaseItem {
  id?: number | string;
  line_number?: number;
  item_name: string;
  specification?: string;
  quantity: number;
  unit_price_value?: number;
  amount_value?: number;
  remark?: string;
  link?: string;
  is_received?: boolean;
  delivery_status?: string;
  receipt_image_url?: string | null;
  receipt_uploaded_at?: string | null;
  receipt_uploaded_by?: string | null;
}

interface PurchaseItemsModalProps {
  isOpen: boolean;
  onClose: () => void;
  purchase: {
    id: number;
    purchase_order_number?: string;
    vendor_name: string;
    requester_name: string;
    project_vendor: string;
    sales_order_number: string;
    project_item: string;
    request_date: string;
    delivery_request_date?: string;
    currency: string;
    items?: PurchaseItem[];
    total_amount: number;
  };
  isAdmin: boolean;
  onUpdate: () => void;
}

export default function PurchaseItemsModal({ isOpen, onClose, purchase, isAdmin, onUpdate }: PurchaseItemsModalProps) {
  const [editingItems, setEditingItems] = useState<PurchaseItem[]>(purchase.items || []);
  const [isEditing, setIsEditing] = useState(false);
  const supabase = createClient();
  
  // ÌíàÎ™© ÏàòÏ†ï ÏãúÏûë
  const handleEditStart = () => {
    setIsEditing(true);
    setEditingItems([...purchase.items || []]);
  };

  // ÌíàÎ™© ÏàòÏ†ï Ï∑®ÏÜå
  const handleEditCancel = () => {
    setIsEditing(false);
    setEditingItems(purchase.items || []);
  };

  // ÌíàÎ™© Í∞í Î≥ÄÍ≤Ω
  const handleItemChange = (index: number, field: keyof PurchaseItem, value: any) => {
    const newItems = [...editingItems];
    newItems[index] = {
      ...newItems[index],
      [field]: field === 'quantity' || field === 'unit_price_value' || field === 'amount_value' ? Number(value) : value
    };
    
    // Í∏àÏï° ÏûêÎèô Í≥ÑÏÇ∞ (amount_valueÎ•º ÏßÅÏ†ë ÏàòÏ†ïÌïòÎäî Í≤ΩÏö∞Í∞Ä ÏïÑÎãê ÎïåÎßå)
    if (field === 'quantity' || field === 'unit_price_value') {
      newItems[index].amount_value = newItems[index].quantity * (newItems[index].unit_price_value || 0);
    }
    
    setEditingItems(newItems);
  };

  // ÏÉà ÌíàÎ™© Ï∂îÍ∞Ä
  const handleAddItem = () => {
    const newItem: PurchaseItem = {
      line_number: editingItems.length + 1,
      item_name: '',
      specification: '',
      quantity: 0,
      unit_price_value: 0,
      amount_value: 0,
      remark: '',
      is_received: false
    };
    setEditingItems([...editingItems, newItem]);
  };

  // ÌíàÎ™© ÏÇ≠Ï†ú
  const handleDeleteItem = (index: number) => {
    const newItems = editingItems.filter((_, i) => i !== index);
    // line_number Ïû¨Ï†ïÎ†¨
    newItems.forEach((item, i) => {
      item.line_number = i + 1;
    });
    setEditingItems(newItems);
  };

  // ÌíàÎ™© Ï†ÄÏû•
  const handleSave = async () => {
    try {
      // Í∏∞Ï°¥ ÌíàÎ™© ÏÇ≠Ï†ú
      await supabase
        .from('purchase_request_items')
        .delete()
        .eq('purchase_request_id', purchase.id);

      // ÏÉà ÌíàÎ™© Ï∂îÍ∞Ä
      const itemsToInsert = editingItems.map(item => ({
        purchase_request_id: purchase.id,
        purchase_order_number: purchase.purchase_order_number,
        line_number: item.line_number,
        item_name: item.item_name,
        specification: item.specification,
        quantity: item.quantity,
        unit_price_value: item.unit_price_value,
        amount_value: item.amount_value,
        remark: item.remark,
        link: item.link,
        is_received: item.is_received || false,
        delivery_status: item.delivery_status || 'pending'
      }));

      const { error } = await supabase
        .from('purchase_request_items')
        .insert(itemsToInsert);

      if (error) throw error;

      // Ï¥ùÍ∏àÏï° ÏóÖÎç∞Ïù¥Ìä∏
      const totalAmount = editingItems.reduce((sum, item) => sum + (item.amount_value || 0), 0);
      await supabase
        .from('purchase_requests')
        .update({ total_amount: totalAmount })
        .eq('id', purchase.id);

      toast.success('ÌíàÎ™©Ïù¥ ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§.');
      setIsEditing(false);
      onUpdate();
      onClose();
    } catch (error) {
      toast.error('Ï†ÄÏû• Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
    }
  };
  
  const items = isEditing ? editingItems : (purchase.items || []);
  const totalAmount = items.reduce((sum, item) => sum + (item.amount_value || 0), 0);
  
  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="w-full max-w-[95vw] sm:max-w-6xl max-h-[80vh] overflow-hidden flex flex-col bg-white">
        <DialogHeader className="flex-shrink-0">
          <div className="flex items-center justify-between">
            <DialogTitle className="text-xl font-bold">
              Î∞úÏ£º ÏÉÅÏÑ∏ Ìï≠Î™© - {purchase.purchase_order_number}
            </DialogTitle>
            <div className="flex items-center gap-2">
              {isAdmin && !isEditing && (
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleEditStart}
                >
                  <Edit2 className="h-4 w-4 mr-1" />
                  Ìé∏Ïßë
                </Button>
              )}
              {isEditing && (
                <>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={handleAddItem}
                  >
                    <Plus className="h-4 w-4 mr-1" />
                    ÌíàÎ™© Ï∂îÍ∞Ä
                  </Button>
                  <Button
                    variant="default"
                    size="sm"
                    onClick={handleSave}
                  >
                    <Save className="h-4 w-4 mr-1" />
                    Ï†ÄÏû•
                  </Button>
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={handleEditCancel}
                  >
                    Ï∑®ÏÜå
                  </Button>
                </>
              )}
              <Button
                variant="ghost"
                size="icon"
                onClick={onClose}
                className="h-6 w-6"
              >
                <X className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </DialogHeader>
        
        <div className="flex-shrink-0 grid grid-cols-2 md:grid-cols-4 gap-4 py-4 border-b">
          <div>
            <p className="text-sm text-gray-500">ÏóÖÏ≤¥Î™Ö</p>
            <p className="font-medium">{purchase.vendor_name}</p>
          </div>
          <div>
            <p className="text-sm text-gray-500">ÏöîÏ≤≠Ïûê</p>
            <p className="font-medium">{purchase.requester_name}</p>
          </div>
          <div>
            <p className="text-sm text-gray-500">ÌîÑÎ°úÏ†ùÌä∏</p>
            <p className="font-medium truncate" title={purchase.project_vendor}>
              {purchase.project_vendor}
            </p>
          </div>
          <div>
            <p className="text-sm text-gray-500">ÎÇ©Í∏∞Ïùº</p>
            <p className="font-medium">
              {purchase.delivery_request_date && 
                format(new Date(purchase.delivery_request_date), 'yyyy-MM-dd')}
            </p>
          </div>
        </div>
        
        <div className="flex-1 overflow-auto">
          <Table>
            <TableHeader className="sticky top-0 bg-white z-10">
              <TableRow>
                <TableHead className="w-12">No.</TableHead>
                <TableHead>ÌíàÎ™©</TableHead>
                <TableHead>Í∑úÍ≤©</TableHead>
                <TableHead className="text-right">ÏàòÎüâ</TableHead>
                <TableHead className="text-right">Îã®Í∞Ä</TableHead>
                <TableHead className="text-right">Í∏àÏï°</TableHead>
                <TableHead>ÏûÖÍ≥†ÏÉÅÌÉú</TableHead>
                <TableHead>ÏòÅÏàòÏ¶ù</TableHead>
                <TableHead>ÎπÑÍ≥†</TableHead>
                {isEditing && <TableHead className="w-20">ÏÇ≠Ï†ú</TableHead>}
              </TableRow>
            </TableHeader>
            <TableBody>
              {items.map((item, index) => (
                <TableRow key={item.id || index}>
                  <TableCell className="font-medium">{item.line_number || index + 1}</TableCell>
                  <TableCell>
                    {isEditing ? (
                      <Input
                        value={item.item_name}
                        onChange={(e) => handleItemChange(index, 'item_name', e.target.value)}
                        className="h-8 text-sm"
                      />
                    ) : (
                      <div className="sm:max-w-[200px]">
                        <p className="font-medium truncate" title={item.item_name}>
                          {item.item_name}
                        </p>
                      </div>
                    )}
                  </TableCell>
                  <TableCell>
                    {isEditing ? (
                      <Input
                        value={item.specification}
                        onChange={(e) => handleItemChange(index, 'specification', e.target.value)}
                        className="h-8 text-sm"
                      />
                    ) : (
                      <div className="sm:max-w-[150px] truncate" title={item.specification}>
                        {item.specification}
                      </div>
                    )}
                  </TableCell>
                  <TableCell className="text-right">
                    {isEditing ? (
                      <Input
                        type="number"
                        value={item.quantity}
                        onChange={(e) => handleItemChange(index, 'quantity', e.target.value)}
                        className="h-8 text-sm text-right"
                      />
                    ) : (
                      <span className="font-medium">{item.quantity.toLocaleString()}</span>
                    )}
                  </TableCell>
                  <TableCell className="text-right">
                    {isEditing ? (
                      <Input
                        type="number"
                        value={item.unit_price_value}
                        onChange={(e) => handleItemChange(index, 'unit_price_value', e.target.value)}
                        className="h-8 text-sm text-right"
                      />
                    ) : (
                      <span>{(item.unit_price_value || 0).toLocaleString()} {purchase.currency}</span>
                    )}
                  </TableCell>
                  <TableCell className="text-right">
                    {isEditing ? (
                      <Input
                        type="number"
                        value={item.amount_value}
                        onChange={(e) => handleItemChange(index, 'amount_value', e.target.value)}
                        className="h-8 text-sm text-right font-medium"
                      />
                    ) : (
                      <span className="font-medium">{(item.amount_value || 0).toLocaleString()} {purchase.currency}</span>
                    )}
                  </TableCell>
                  <TableCell>
                    {item.is_received ? (
                      <Badge className="bg-green-100 text-green-800">ÏûÖÍ≥†ÏôÑÎ£å</Badge>
                    ) : (
                      <Badge variant="outline">ÎåÄÍ∏∞</Badge>
                    )}
                  </TableCell>
                  <TableCell>
                    {/* ÏòÅÏàòÏ¶ù Îã§Ïö¥Î°úÎìú Î≤ÑÌäº */}
                    <ReceiptDownloadButton
                      itemId={Number(item.id)}
                      receiptUrl={item.receipt_image_url}
                      itemName={item.item_name}
                      onUpdate={onUpdate}
                    />
                  </TableCell>
                  <TableCell>
                    {isEditing ? (
                      <Input
                        value={item.remark || ''}
                        onChange={(e) => handleItemChange(index, 'remark', e.target.value)}
                        className="h-8 text-sm"
                        placeholder="ÎπÑÍ≥†"
                      />
                    ) : (
                      <div className="sm:max-w-[150px]">
                        <span className="text-sm text-gray-500 truncate block" title={item.remark}>
                          {item.remark || '-'}
                        </span>
                      </div>
                    )}
                  </TableCell>
                  {isEditing && (
                    <TableCell>
                      <Button
                        variant="ghost"
                        size="icon"
                        onClick={() => handleDeleteItem(index)}
                        className="h-8 w-8 text-red-500 hover:text-red-700"
                      >
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    </TableCell>
                  )}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
        
        <div className="flex-shrink-0 border-t pt-4 flex justify-between items-center">
          <div className="text-sm text-gray-500">
            Ï¥ù {items.length}Í∞ú ÌíàÎ™©
          </div>
          <div className="text-lg font-bold">
            Ï¥ù Í∏àÏï°: {totalAmount.toLocaleString()} {purchase.currency}
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}